@import url("https://fonts.googleapis.com/css?family=Raleway:300,700,400|Roboto:300,400|PT+Sans:400|Inter:300,700,300italic");
@font-face {
    font-family: "Font Awesome 5 Free-Solid";
    font-style: normal;
    font-weight: 400;
    src: url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/webfonts/fa-solid-900.woff2') format('woff2'),
         url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/webfonts/fa-solid-900.woff') format('woff');
  }

  @font-face {
    font-family: "Font Awesome 5 Free-Regular";
    font-style: normal;
    font-weight: 400;
    src: url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/webfonts/fa-regular-400.woff2') format('woff2'),
         url('https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.15.3/webfonts/fa-regular-400.woff') format('woff');
  }

 /* Styleguide */ 
 :root {
   /* COLORS */
   --gray-nurse: rgba(233,233,233,1);
   --white: rgba(255,255,255,1);
   --mine-shaft: rgba(51,51,51,1);
   --chathams-blue: rgba(17,81,114,1);
   --black: rgba(0,0,0,1);

   --font-size-s: 14px;
   --font-family-font_awesome_5_free-solid: "Font Awesome 5 Free-Solid";
   --font-family-raleway: "Raleway";
   --font-family-pt_sans: "PT Sans";
 }
 

.valign-text-middle {
   display: flex;
   flex-direction: column;
   justify-content: center;
 }

.ptsans-normal-black-18px {
   color: var(--black);
   font-family: var(--font-family-pt_sans);
   font-size: 18px;
   font-weight: 400;
   font-style: normal;
}

.raleway-normal-mine-shaft-14px-2 {
   color: var(--mine-shaft);
   font-family: var(--font-family-raleway);
   font-size: var(--font-size-s);
   font-weight: 400;
   font-style: normal;
}

.raleway-bold-chathams-blue-14px {
   color: var(--chathams-blue);
   font-family: var(--font-family-raleway);
   font-size: var(--font-size-s);
   font-weight: 700;
   font-style: normal;
}

.fontawesome5free-solid-normal-chathams-blue-16-8px {
   color: var(--chathams-blue);
   font-family: var(--font-family-font_awesome_5_free-solid);
   font-size: 16.8px;
   font-weight: 400;
   font-style: normal;
}
  /* Stylesheet */ 

  .article {
     align-items: center;
     background-color: var(--white);
     border: 1px solid;
     border-color: var(--gray-nurse);
     display: flex;
     flex-direction: column;
     gap: 28.36px;
     padding: 29.36px 21px 11px 21px;
     position: relative;
     width: 262.77px;
  }


  .diviconmain {
     height: 179.8px;
     min-width: 220.77px;
     position: relative;
  }

  .flex-col {
     align-items: center;
     display: flex;
     flex-direction: column;
     min-height: 164px;
     padding: 0 0.5px;
     width: 221px;
  }

  .community-health-initiatives {
     height: 36px;
     letter-spacing: 0;
     line-height: 18px;
     margin-left: 0.07px;
     margin-top: -1px;
     min-width: 143px;
     position: relative;
     text-align: center;
  }

  .technical-assistance {
     height: 76px;
     letter-spacing: 0;
     line-height: 25.2px;
     margin-left: 0.83px;
     margin-top: 17px;
     min-width: 219px;
     position: relative;
     text-align: center;
  }

  .link {
     align-items: flex-start;
     display: flex;
     gap: 4px;
     height: 16px;
     margin-top: 20px;
     min-width: 80px;
  }

  .read-more {
     height: 26px;
     letter-spacing: 0;
     line-height: 25.2px;
     margin-top: -6px;
     min-width: 73px;
     text-align: center;
     white-space: nowrap;
  }

  .text {
     height: 31px;
     letter-spacing: 0;
     line-height: 30.2px;
     margin-top: -9px;
     min-width: 9px;
     text-align: center;
     white-space: nowrap;
  }

  .p {
   align-items: flex-start;
   display: flex;
   padding: 0px 63.23px 0.38px 0px;
   position: relative;
   width: 360.23px;
}